"use client"
import { useState, useEffect } from "react";

const BackToTop = () => {
  const [isVisible, setIsVisible] = useState(false);

  // ĞŸĞ¾ĞºĞ°Ğ·Ğ°Ñ‚ÑŒ ĞºĞ½Ğ¾Ğ¿ĞºÑƒ, ĞºĞ¾Ğ³Ğ´Ğ° ÑĞºÑ€Ğ¾Ğ»Ğ»Ğ¸Ğ¼ Ğ²Ğ½Ğ¸Ğ·
  const toggleVisibility = () => {
    if (window.scrollY > 200) {
      setIsVisible(true);
    } else {
      setIsVisible(false);
    }
  };

  // ĞŸĞ»Ğ°Ğ²Ğ½Ğ°Ñ Ğ¿Ñ€Ğ¾ĞºÑ€ÑƒÑ‚ĞºĞ° Ğ½Ğ°Ğ²ĞµÑ€Ñ…
  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  };

  useEffect(() => {
    window.addEventListener("scroll", toggleVisibility);

    return () => {
      window.removeEventListener("scroll", toggleVisibility);
    };
  }, []);

  return (
    isVisible && (
      <div
        onClick={scrollToTop}
        className="fixed bottom-10 right-10 text-white p-3 rounded-full backToTopBtn transition cursor-pointer"
      >
        â†‘
      </div>
    )
  );
};

export default BackToTop;